import 'bootstrap/dist/css/bootstrap.min.css'
import './App.css';
import AddTodo from './components/AddTodo';
import ViewTodo from './components/ViewTodo';
import {collection,
query,
onSnapshot,
doc,
updateDoc,
deleteDoc,} from "firebase/firestore";
import { db } from './firebase';

import React,{useState,useEffect} from 'react';
function App() {
  const [todos, setTodos] = useState([]);
  useEffect(() => {
    const q=query(collection(db,"todos"));
    const unsub=onSnapshot(q,(querySnapshot)=>{
      let todosArray=[];
      querySnapshot.forEach((doc)=>{
        todosArray.push({...doc.data(),id:doc.id})

      });
      setTodos(todosArray);
    });
    return ()=> unsub();

  }, []);
  
  const handleEdit=async(todo,title)=>{
    await updateDoc(doc(db,"todos",todo.id),{title:title});

  };

  const toggleComplete=async(todo)=>{
    await updateDoc(doc(db,"todos",todo.id),{completed: !todo.completed});

  };
  const handleDelete=async(id)=>{
    await deleteDoc(doc(db,"todos",id));

  };
  return (
    <div className="App">
      <div className="App list-group-item justify-content-center align-items-center mx-auto " style={{"width":"500px","backgroundColor":"white","marginTop":"15px","marginRight":"2px","marginLeft":"2px","boxShadow": 
    "12px 12px 16px 0 rgba(0, 0, 0, 0.25) ,-8px -8px 12px 0 rgba(255, 255, 255, 0.3)"
    }}>
        <h1 className="card1 bg-dark text-white mb-1" stylename="max-width:20rem;" >Task Manager</h1>
        <h5 className="card bg-warning text-white mb-3" stylename="max-width:20rem;">Add Your Tasks</h5>
        <span className="text">
        
        
        <AddTodo/>
        
        </span>
        <h4 className="card bg-dark text-white mb-3" stylename="max-width:20rem;">Your Tasks</h4>
        <div className='todo_container'>
          {todos.map((todo)=>(<ViewTodo key={todo.id} todo={todo} toggleComplete={toggleComplete} handleDelete={handleDelete} handleEdit={handleEdit}/>))}
          
        </div>
        <h4 className="card bg-info text-white mb-2 py-2" >Copyright © 2022 Rohan. All rights reserved.</h4>
        
      </div>
      
    </div>
  );
}

export default App;
