import React,{useState} from 'react'
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';

export default function ViewTodo({
    todo,
    toggleComplete,
    handleDelete,
    handleEdit,
}) {
    const [newTitle,setNewTitle]=useState(todo.title)

    const handleChange=(e)=>{
        e.preventDefault();
        if(todo.completed===true){
            setNewTitle(todo.title);
        }else{
            todo.title="";
            setNewTitle(e.target.value);
        }
;
    }
  return (
    < div className="todo">
        
        <input type="text" style={{textDecoration:todo.completed && "line-through",width:'300px'}} value={todo.title===""?newTitle:todo.title}className="list" onChange={handleChange}/>
       
            <button className="button-complete" onClick={()=>toggleComplete(todo)}><CheckCircleIcon id="i" color='success'/></button>
            <button className="button-edit" onClick={()=>handleEdit(todo,newTitle)}><EditIcon id="i" color='primary'/></button>
            <button className="button-delete" onClick={()=>handleDelete(todo.id)}><DeleteIcon id="i" sx={{ color: 'red'}}/></button>
        
    </div>
  )
}
