import React,{useState} from 'react'
import {db} from "../firebase";
import { collection,addDoc } from 'firebase/firestore';


export default function AddTodo() {
    const [title, setTitle] = useState("");
    const handleClick= async(e)=>{
        e.preventDefault();
        if(title!==""){
            await addDoc(collection(db,"todos"),{title,completed:false,});
            setTitle("")
        }
    };
  return (
    <div>
        <input className="form-control  mb-3" placeholder='Enter your task' value={title} onChange={(e)=>setTitle(e.target.value)} />
        <button className="btn btn-outline-primary mx-2 mb-3" style={{borderRadius:"20px",fontWeight:"bold"}} onClick={handleClick}>Add Task</button>
    </div>
  )
}
